.promo {
  background: url("../img/promo-background-desk@1x.png") round;
  background-color: #1b2d37;
  min-height: 879px; }

.promo__description {
  display: flex;
  flex-direction: row;
  max-width: 1580px;
  margin: 0 auto;
  padding: 0 71px;
  background: url("../img/board-desktop@1x.png") 868px 175px no-repeat, url("../img/promo-polygon.png") 783px 55px no-repeat;
  color: #ffffff; }

.promo__description-text {
  display: flex;
  flex-direction: column;
  width: 60%;
  padding-left: 8px;
  padding-top: 230px;
  padding-bottom: 160px; }

.promo__description-text p {
  position: relative;
  padding-top: 6px;
  padding-left: 99px;
  padding-right: 60px;
  font-size: 22px;
  line-height: 34px; }

.promo__description-text p::before {
  content: "";
  position: absolute;
  width: 66px;
  height: 2px;
  top: 23px;
  left: 3px;
  background-color: #ffffff; }

.promo__title {
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase; }

.promo__link--advice {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 407px;
  min-height: 55px;
  padding: 0 26px;
  margin-right: 4px;
  margin-left: 96px;
  margin-top: 23px;
  background-color: #00b2ff;
  border-radius: 5px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center; }

.promo__link--scroll {
  width: 100px;
  transform: rotate(-90deg) translate(-127px, -39px);
  font-family: "PT Mono", "Courier", monospace;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.5; }

@media (max-width: 1023px) {
  .promo {
    background: url("../img/promo-background-desk@1x.png") no-repeat;
    background-color: #1b2d37; }
  .promo__title {
    font-weight: bold;
    font-size: 60px;
    line-height: 70px;
    letter-spacing: 0.07em;
    text-transform: uppercase; }
  .promo__description-text {
    width: 72%;
    padding-left: 16px;
    padding-top: 117px; }
  .promo__description-text p {
    font-size: 18px;
    line-height: 28px;
    padding-top: 11px;
    padding-left: 52px;
    padding-right: 45px; }
  .promo__link--advice {
    margin-left: 52px;
    margin-top: 37px; }
  .promo__description-text p::before {
    width: 34px;
    top: 27px; }
  .promo__description {
    background: url("../img/board-tablet@1x.png") right 42px no-repeat, url("../img/promo-polygon-tablet.png") right -10px no-repeat; }
  .promo__link--scroll:not(:focus):not(:active) {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden; } }

@media (max-width: 767px) {
  .promo__title {
    font-size: 40px;
    line-height: 47px; }
  .promo__description-text p {
    font-size: 16px;
    line-height: 25px;
    padding-left: 51px;
    padding-right: 15px; }
  .promo__description-text {
    width: 100%;
    padding-top: 231px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 70px; }
  .promo__description {
    padding: 0; }
  .promo__link--advice {
    margin: 0;
    margin-top: 24px; }
  .promo__description {
    background: url("../img/board-mobile@1x.png") right 37px no-repeat, url("../img/promo-polygon-mobile.png") right -25px no-repeat; } }

.page-header {
  display: flex;
  max-width: 1437px;
  flex-direction: row;
  margin: 0 auto;
  padding: 0 10px; }

@media (max-width: 1023px) {
  .page-header {
    padding: 0; } }

.main-nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  padding-top: 32px; }

.main-nav__logo {
  display: flex;
  flex-direction: row; }

.main-nav__logo-text {
  padding-left: 18px;
  padding-top: 6px; }

.main-nav__logo-text p {
  margin: 0; }
  .main-nav__logo-text p:first-child {
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-weight: bold;
    font-size: 25px;
    line-height: 29px; }
  .main-nav__logo-text p:nth-child(2) {
    font-family: "Roboto", "Arial", sans-serif;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: #788891; }

.main-nav__menu {
  display: block;
  position: relative;
  width: 40px;
  height: 20px;
  margin-left: 116px;
  cursor: pointer;
  background: none;
  border: none; }

.main-nav--closed .main-nav__menu::before {
  content: "";
  position: absolute;
  top: 4px;
  left: 1px;
  width: 37px;
  height: 1px;
  background-color: #ffffff;
  box-shadow: 0 12px 0 0 #ffffff; }

.main-nav__link {
  display: flex;
  align-items: center;
  margin-right: 50px;
  font-family: "PT Mono", "Courier", monospace;
  font-size: 16px;
  line-height: 18px; }

.main-nav__link--mail {
  margin-right: 90px; }

.main-nav__link--mail span {
  margin-left: 19px; }

.main-nav__link--tel span {
  padding-left: 14px; }

.main-nav__feedback {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 180px;
  min-height: 45px;
  padding: 0 26px;
  margin-right: 4px;
  border: 1px solid #ffffff;
  border-radius: 5px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase; }

.main-nav__contact {
  display: flex;
  flex-direction: row;
  margin-left: auto; }

@media (max-width: 1100px) {
  .main-nav__menu {
    margin-left: auto; } }

@media (max-width: 1023px) {
  .main-nav {
    padding-top: 19px;
    padding-right: 25px;
    padding-left: 25px; }
  .main-nav__link span:not(:focus):not(:active) {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden; }
  .main-nav__feedback:not(:focus):not(:active) {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden; }
  .main-nav__link--mail svg {
    width: 26px;
    height: 19px; }
  .main-nav__link--tel svg {
    width: 24px;
    height: 24px; }
  .main-nav__menu {
    margin-top: -4px;
    margin-left: auto; }
  .main-nav__contact {
    margin-left: 0; }
  .main-nav__link {
    padding-left: 34px;
    margin-right: 0; } }

@media (max-width: 767px) {
  .main-nav__logo-text:not(:focus):not(:active) {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden; }
  .main-nav__logo img {
    width: 38px;
    height: 38px; }
  .main-nav {
    padding-top: 17px;
    padding-left: 17px; }
  .main-nav__contact {
    margin-right: -10px;
    margin-bottom: 4px; }
  .main-nav__link {
    padding-left: 23px; } }
